<template>
   <div class="tab-control">
       <div v-for="(item, index) in titles" class="tab-control-item" 
       :class="{active: index === currentIndex}" 
       @click="itemClick(index)">
           <span>{{item}}</span>
       </div>
    </div> 
</template>

<script>
export default {
    name: 'TabControl',
    props: {
        titles: {
            type: Array,
            default() {
                return []
            }
        }
    },
    data() {
        return {
            currentIndex: 0
        }
    },
    methods: {
        itemClick(index) {
            this.currentIndex = index;
            this.$emit('tabClick',index)
        }
    }
}
</script>

<style>
.tab-control {
    display: flex;
    text-align: center;
    background-color: #fff;
}
.tab-control-item {
    flex: 1;
    height: 40px;
    line-height: 40px;
    font-size: 15px;
}
.tab-control-item span {
    padding: 5px;
}
.active {
    color: skyblue;
}
.active span {
    border-bottom: 3px skyblue solid;
}
</style>